.subTitle {
  opacity: 1;
  width: 100vw;
  font-size: 6rem;
  color: #04172F;
  text-shadow: 6px 6px 10px #010c18, -6px -6px 10px #082850;
  -webkit-animation-name: abtTitle;
          animation-name: abtTitle;
  -webkit-animation-duration: 3.5s;
          animation-duration: 3.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  animation-direction: reverse;
}

#subTitle {
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

@-webkit-keyframes abtTitle {
  0% {
    text-shadow: 6px 6px 10px #010c18, -6px -6px 10px #082850;
  }
  50% {
    text-shadow: 0 0 0 #010c18, 0 0 0 #082850;
  }
  55% {
    opacity: 0;
  }
  63% {
    text-shadow: 0 0 0 #010c18, 0 0 0 #082850;
  }
  100% {
    text-shadow: 6px 6px 10px #010c18, -6px -6px 10px #082850;
  }
}

@keyframes abtTitle {
  0% {
    text-shadow: 6px 6px 10px #010c18, -6px -6px 10px #082850;
  }
  50% {
    text-shadow: 0 0 0 #010c18, 0 0 0 #082850;
  }
  55% {
    opacity: 0;
  }
  63% {
    text-shadow: 0 0 0 #010c18, 0 0 0 #082850;
  }
  100% {
    text-shadow: 6px 6px 10px #010c18, -6px -6px 10px #082850;
  }
}

