.menuContainer{
    display: flex;
    flex-direction: row;
    height: 180px;
}

.menu{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 80px;
    width: 80px;
    background-color: #5B92A7;
    border-radius: 0 0 100% 0;
    cursor: pointer;
}

.menu:focus{
    outline: none;
}

.menu-line{
    width: 50px;
    height: 5px;
    background-color: #ffffff;
    border-radius: 5px;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.menu-line2{
    width: 40px;
    height: 5px;
    background-color: #ffffff;
    border-radius: 5px;
    margin: 8px 0;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.menu-line3{
    width: 30px;
    height: 5px;
    background-color: #ffffff;
    border-radius: 5px;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.menu-line.open{
    width: 45px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.menu-line2.open{
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
    background: none;
    margin: 0;
}

.menu-line3.open{
    top: 0;
    width: 45px;
    -webkit-transform: rotate(-45deg) translate(7px, -7px);
            transform: rotate(-45deg) translate(7px, -7px);
}

.menuBtn{
    position: relative;
}

.icon{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

