    background-color: #5B92A7;
    border-radius: 50%;
}

.burgerLink:visited{
    color: #ffffff;
}

.none{
    position: absolute;
    visibility: hidden;
}

.show{
    position: absolute;
    -webkit-animation-name: showBtn;
            animation-name: showBtn;
    -webkit-animation-duration: .2s;
            animation-duration: .2s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    z-index: 3;
}

@-webkit-keyframes showBtn{
    0% {opacity: 0;}
    10% {opacity: 0;}
    100% {-webkit-transform: translateX(45px);transform: translateX(45px); opacity: 1;}
}

@keyframes showBtn{
    0% {opacity: 0;}
    10% {opacity: 0;}
    100% {-webkit-transform: translateX(45px);transform: translateX(45px); opacity: 1;}
}

.show2{
