.contact-info{
    display: flex;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    flex-direction: column;
}

.contact-box{
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    flex-direction: column;
    box-shadow: 
    6px 6px 10px #041831, 
    -6px -6px 10px #09305f;
    border-radius: 10px;
    width: 600px;
    height: 120px;
    margin-top: 30px;
}

.cInfo{
    margin: 5px;
    font-size: 18px;
}

.contact-logos{
    display: flex;
    flex-direction: row;
    margin-top: 100px;
    justify-content: center;
    width: 100vw;
}

.contact{
    margin-left: 80px;
    margin-right: 80px;
}

.contact:hover{
    -webkit-transform: translate( 0px, -30px);
            transform: translate( 0px, -30px);
    cursor: pointer;
}

.linkedIn:link{
    color: #0077B7;
}

.linkedIn:visited{
    color: #0077B7;
}

#linkedInLogo{
    position: relative;
}

.linkedIn2{
    background-color: #ffffff;
    position: absolute;
    top: 20px;
    left: 10px;
    height: 100px;
    width: 100px;
    z-index: -1;
}

#emailLogo{
    position: relative;
}

.email2{
    position: absolute;
    background-color: #ffffff;
    height: 60px;
    width: 100%;
    z-index: -1;
    top: 60px;
}

.email2::before{
    content: "";
    background-color: #ffffff;
    position: absolute;
    height: 100px;
    width: 95px;
    bottom: 0;
    left: 25px;
}

.email3{
    position: absolute;
    background-color: #000000;
    height: 15px;
    width: 60px;
    top: 55px;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border-radius: 10px;
}

.email3::before{
    content: "";
    background-color: #000000;
    position: absolute;
    height: 15px;
    width: 60px;
    top: 25px;
    border-radius: 10px;
}

.email:link{
    color: #F8C843;
}

.email:visited{
    color: #F8C843;
}

#githubLogo{
    position: relative;
}

.github2{
    background-color: #ffffff;
    position: absolute;
    height: 139px;
    width: 139px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border-radius: 50%;
    z-index: -1;
}

.github:link{
    color: black;
}

.github:visited{
    color: black;
}

@media only screen and (max-width: 600px){
    .contact-box{
        width: 300px;
        height: 110px;
    }
